import { values } from 'core-js-pure/es/object';

const formatPovertyData = (array, color) => {
  const obj = {};

  // eslint-disable-next-line
  array.map((d, i) => {
    if (i === 0) {
      const arrObj = Object.keys(d).map((year, i) => {
        return (obj[i] = {
          x: new Date(year, 1, 1),
          fill: color,
          label: `Year: ${year}\n`,
        });
      });
      values(d).map((value, i) => {
        arrObj[i].y = parseFloat(value);
        arrObj[i].label = `${arrObj[i].label}Poverty Rate: ${value}%\n`;
        return arrObj;
      });
      return i <= 7 ? arrObj.slice(0, 7) : null;
    }
    values(d).map((value, i) => {
      obj[i].size = parseFloat(value) / 20;
      obj[i].label = `${obj[i].label}Number of Poor: ${value}M`;
      return obj;
    });
  });

  const arr = [obj];
  delete arr[0][7];
  return values(arr[0]);
};

export default formatPovertyData;
