// External Imports
import React, { Component } from 'react';
import * as d3 from 'd3';
import * as topojson from 'topojson';

// Internal Imports
import './ChoroplethV2Mongolia.css';

// Data
import combinedData from '../../../data/ChoroplethData/EAP_topojson.json';

class ChoroplethV2Mongolia extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mngCollection: null,
      containerHeight: null,
      containerWidth: null,
      mapCenter: null,
      featureCollection: null,
    };
    this.ChoroplethV2MongoliaContainerRef = React.createRef();
  }

  componentDidMount() {
    const ChoroplethV2MongoliaContainerHeight = this
      .ChoroplethV2MongoliaContainerRef.current.clientHeight;
    const ChoroplethV2MongoliaContainerWidth = this
      .ChoroplethV2MongoliaContainerRef.current.clientWidth;

    const featureCollection = topojson.feature(
      combinedData,
      combinedData.objects.eap_subnatid_povdata
    );

    const mngIsoloated = featureCollection.features.filter((country) => {
      return ['MNG'].includes(country.properties.cntrycd);
    });

    const mngCollection = {
      features: mngIsoloated,
      type: 'FeatureCollection',
    };

    const center = d3.geoPath().centroid(mngCollection);

    this.setState(
      {
        mngCollection: mngCollection,
        containerHeight: ChoroplethV2MongoliaContainerHeight,
        containerWidth: ChoroplethV2MongoliaContainerWidth,
        mapCenter: center,
        featureCollection: featureCollection,
      },
      () => {
        this.renderMap();
      }
    );

    window.addEventListener('resize', this.onResize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onResize);
  }

  renderMap = () => {
    const {
      mngCollection,
      containerHeight,
      containerWidth,
      mapCenter,
    } = this.state;

    const svg = d3
      .select('.ChoroplethV2Mongolia-container')
      .append('svg')
      .attr('class', 'ChoroplethV2Mongolia-svg')
      .attr('height', containerHeight)
      .attr('width', containerWidth);

    const offset = [containerWidth / 2.1, containerHeight / 2.25];
    const scale = this.getScale();

    const projection = d3
      .geoMercator()
      .scale(scale)
      .center(mapCenter)
      .translate(offset);

    const path = d3.geoPath().projection(projection);

    svg
      .append('g')
      .attr('class', 'country')
      .selectAll('path')
      .data(mngCollection.features)
      .enter()
      .append('path')
      .attr('class', 'sub-nation')
      .attr('fill', 'lightgrey')
      .attr('d', path)
      .append('title');

    svg
      .append('path')
      .datum(
        topojson.mesh(
          combinedData,
          combinedData.objects.EAP_adm0,
          (a) => a.id === 0
        )
      )
      .attr('fill', 'none')
      .attr('stroke', 'white')
      .attr('stroke-width', '0.5px')
      .attr('stroke-linejoin', 'round')
      .attr('d', path);
  };

  getScale = () => {
    const windowWidth = window.innerWidth;
    const windowHeight = window.innerHeight;
    const baseScale = 550;
    const scaleFactor = 4.6;
    const baseWidth = 300;
    const baseHeight = 150;

    const scale1 = (baseScale * windowWidth) / baseWidth;
    const scale2 = (baseScale * windowHeight) / baseHeight;

    return d3.min([scale1, scale2]) / scaleFactor;
  };

  onResize = () => {
    const { mapCenter } = this.state;

    const containerHeight = this.ChoroplethV2MongoliaContainerRef.current
      .clientHeight;
    const containerWidth = this.ChoroplethV2MongoliaContainerRef.current
      .clientWidth;
    const newScale = this.getScale();
    const newOffset = [containerWidth / 2.1, containerHeight / 2.25];

    const newProjection = d3
      .geoMercator()
      .scale(newScale)
      .center(mapCenter)
      .translate(newOffset);

    const newPath = d3.geoPath().projection(newProjection);

    const svg = d3
      .select('.ChoroplethV2Mongolia-svg')
      .attr('height', containerHeight)
      .attr('width', containerWidth);

    svg.selectAll('path').attr('d', newPath);
  };

  render() {
    return (
      <div
        className="ChoroplethV2Mongolia-container"
        ref={this.ChoroplethV2MongoliaContainerRef}
      >
        <span className="ChoroplethV2Mongolia-span">Mongolia</span>
      </div>
    );
  }
}

export default ChoroplethV2Mongolia;
